a {
  text-decoration: none;
  color: #E0ECFF;
}
.trading-mining {
  border-top: 1px solid #203B60;
}
.trading-mining .check {
  display: flex;
  margin-top: 18px;
  margin-left: 45px;
}
.trading-mining .check div {
  width: 88px;
  height: 31px;
  background: rgba(85, 119, 253, 0.1);
  margin-left: 7px;
  border-radius: 4px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #E0ECFF;
  cursor: pointer;
}
.trading-mining .check div a {
  text-decoration: none;
  color: #E0ECFF;
}
.trading-mining .check .now {
  background: #5577FD;
}
.trading-mining .upcoming-box {
  margin-top: 28px;
  margin-left: 50px;
}
.trading-mining .now-prom {
  width: 1250px;
  margin-top: 28px;
  margin-left: 50px;
}
.trading-mining .finished-box {
  width: 1250px;
  margin: 0 auto;
  margin-top: 4px;
}
.trading-mining .finished-box .finished-epoch {
  height: 360px;
  border-bottom: 1px solid #203B60;
  margin-top: 40px;
}
.trading-mining .finished-box .finished-epoch .finished-epoch-box {
  height: 320px;
  background: url('./img/finished-bg-md.png');
  background-repeat: no-repeat;
  background-size: 100% 100%;
  position: relative;
}
.trading-mining .finished-box .finished-epoch .finished-epoch-box .finished-logo {
  position: absolute;
  background-color: #3756CD;
  top: 1px;
  right: 1px;
  width: 160px;
  height: 56px;
  border-radius: 0 0 0 20px;
  font-size: 24px;
  color: #FFFFFF;
  text-align: center;
  line-height: 56px;
}
.trading-mining .finished-box .finished-epoch .finished-epoch-box .epoch-box {
  padding-top: 70px;
  padding-left: 214px;
  color: #93A1B8;
  font-weight: normal;
}
.trading-mining .finished-box .finished-epoch .finished-epoch-box .epoch-box .epoch-box-title {
  font-size: 32px;
}
.trading-mining .finished-box .finished-epoch .finished-epoch-box .epoch-box .epoch-box-time {
  margin-top: 8px;
}
.trading-mining .finished-box .finished-epoch .finished-epoch-box .epoch-box .epoch-value {
  font-size: 72px;
  font-weight: 600;
  margin-top: 37px;
}
.trading-mining .detailed-rules {
  padding-top: 19px;
  padding-bottom: 19px;
}
@media only screen and (max-width: 759px) {
  .trading-mining .now-prom {
    width: 375px;
    border: 0;
  }
  .trading-mining .finished-box {
    width: 100%;
    margin: 0 auto;
    margin-top: 4px;
  }
  .trading-mining .finished-box .finished-epoch {
    width: 100%;
    height: 360px;
  }
  .trading-mining .finished-box .finished-epoch .finished-epoch-box {
    width: 327px;
    margin: 0 auto;
    background: url('./img/finished-bg-m.png');
    height: 331px;
  }
  .trading-mining .finished-box .finished-epoch .finished-epoch-box .epoch-box {
    padding-left: 21px;
    padding-top: 128px;
  }
  .trading-mining .finished-box .finished-epoch .finished-epoch-box .epoch-box .epoch-value {
    font-size: 50px;
  }
}
@media only screen and (min-width: 760px) and (max-width: 1139px) {
  .trading-mining .now-prom {
    width: 760px;
  }
  .trading-mining .finished-box {
    width: 100%;
    margin: 0 auto;
    margin-top: 4px;
  }
  .trading-mining .finished-box .finished-epoch {
    width: 100%;
    height: 360px;
  }
  .trading-mining .finished-box .finished-epoch .finished-epoch-box {
    width: 730px;
    margin: 0 auto;
    background: url('./img/finished-bg-xs.png');
  }
  .trading-mining .finished-box .finished-epoch .finished-epoch-box .epoch-box {
    padding-left: 193px;
  }
}
@media only screen and (min-width: 1140px) and (max-width: 1270px) {
  .trading-mining .now-prom {
    width: 1135px;
    border: 0;
    border-bottom: 1px solid #93A1B8;
  }
  .trading-mining .finished-box {
    width: 100%;
    margin: 0 auto;
    margin-top: 4px;
  }
  .trading-mining .finished-box .finished-epoch {
    width: 100%;
    margin: 0 auto;
    margin-top: 40px;
  }
  .trading-mining .finished-box .finished-epoch .finished-epoch-box {
    width: 1088px;
    margin: 0 auto;
    background: url('./img/finished-bg-sm.png');
  }
}
