a {
  text-decoration: none;
  color: #E0ECFF;
}
* {
  box-sizing: border-box;
}
.staking {
  width: 100%;
}
.staking .title-timeout {
  padding: 42px 51px 36px 56px;
}
.staking .title-timeout .time-slider {
  background: rgba(255, 255, 255, 0.1);
  border-radius: 200px;
}
.staking .title-timeout .title-time-text .title-text,
.staking .title-timeout .title-time-text .start-end-time-epoch {
  display: flex;
  justify-content: space-between;
}
.staking .title-timeout .title-time-text .title-text {
  font-size: 32px;
  color: #FFFFFF;
}
.staking .title-timeout .title-time-text .start-end-time-epoch {
  margin-top: 8px;
  font-size: 14px;
}
.staking .title-timeout .title-time-text .slider {
  height: 10px;
  margin-top: 28px;
}
.staking .title-timeout .title-time-text .xs-show {
  display: none;
}
.staking .rewards-box {
  padding-left: 56px;
  padding-right: 51px;
  display: flex;
  justify-content: space-between;
}
.staking .rewards-box .deri-text {
  font-size: 11px;
  color: #93A1B8;
  display: inline-block;
  width: 43px;
  height: 20px;
  border-radius: 100px;
  border: 1px solid rgba(224, 236, 255, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 17px;
  margin-left: 10px;
}
.staking .rewards-box .total-rewards-pool {
  width: 579px;
  height: 240px;
  background: url("../img/rewards.png");
  background-repeat: no-repeat;
  padding-left: 37px;
  padding-top: 27px;
}
.staking .rewards-box .total-rewards-pool .deri-logo-bg {
  width: 70px;
  height: 70px;
  background-color: #5577FD;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.staking .rewards-box .total-rewards-pool .total-rewards-num {
  margin-top: 36px;
}
.staking .rewards-box .total-rewards-pool .total-rewards-num .total-rewards-pool-title {
  font-size: 14px;
}
.staking .rewards-box .total-rewards-pool .total-rewards-num .toatl-rewards-deri {
  font-size: 56px;
  color: #FFFFFF;
  display: flex;
  align-items: flex-start;
}
.staking .rewards-box .open-lock-rewards {
  width: 552px;
  height: 239px;
}
.staking .rewards-box .open-lock-rewards .top-open-lock-box,
.staking .rewards-box .open-lock-rewards .bottom-open-lock-box {
  height: 117px;
  display: flex;
  justify-content: space-between;
}
.staking .rewards-box .open-lock-rewards .top-open-lock-box .lock-open,
.staking .rewards-box .open-lock-rewards .bottom-open-lock-box .lock-open {
  width: 271px;
  height: 117px;
  border: 1px dashed #222E40;
  opacity: 0.5;
  position: relative;
  padding-left: 20px;
  padding-top: 13px;
  cursor: pointer;
}
.staking .rewards-box .open-lock-rewards .top-open-lock-box .lock-open .address-reward,
.staking .rewards-box .open-lock-rewards .bottom-open-lock-box .lock-open .address-reward {
  display: flex;
  align-items: center;
  font-size: 32px;
  color: #E0ECFF;
  margin-top: 10px;
}
.staking .rewards-box .open-lock-rewards .top-open-lock-box .lock-open .address-reward .deri-text,
.staking .rewards-box .open-lock-rewards .bottom-open-lock-box .lock-open .address-reward .deri-text {
  margin: 0;
  margin-left: 10px;
}
.staking .rewards-box .open-lock-rewards .top-open-lock-box .lock-open .percentage-reward,
.staking .rewards-box .open-lock-rewards .bottom-open-lock-box .lock-open .percentage-reward {
  margin-top: 6px;
}
.staking .rewards-box .open-lock-rewards .top-open-lock-box .lock-open .lock-logo,
.staking .rewards-box .open-lock-rewards .bottom-open-lock-box .lock-open .lock-logo {
  width: 54px;
  height: 29px;
  position: absolute;
  top: 0;
  right: 0;
  background-color: #112140;
  border-radius: 0 0 0 10px;
  text-align: center;
  line-height: 29px;
  color: #E27625;
}
.staking .rewards-box .open-lock-rewards .top-open-lock-box .open,
.staking .rewards-box .open-lock-rewards .bottom-open-lock-box .open {
  border: 0;
  opacity: 1;
  background-image: url("../img/reward-open.png");
  background-repeat: no-repeat;
}
.staking .rewards-box .open-lock-rewards .top-open-lock-box .open .open-logo,
.staking .rewards-box .open-lock-rewards .bottom-open-lock-box .open .open-logo {
  background-color: #3756CD;
  color: #FFFFFF;
  top: 1px;
  right: 1px;
}
.staking .rewards-box .open-lock-rewards .bottom-open-lock-box {
  margin-top: 6px;
}
.staking .stage-points {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 234px;
  margin-top: 49px;
  border-top: 1px solid #203B60;
  border-bottom: 1px solid #203B60;
}
.staking .stage-points .stage-points-info .top-stage-points .points-box,
.staking .stage-points .stage-points-info .bottom-stage-points .points-box {
  text-align: center;
}
.staking .stage-points .stage-points-info .top-stage-points .points-box .stage-box,
.staking .stage-points .stage-points-info .bottom-stage-points .points-box .stage-box {
  width: 70px;
  height: 22px;
  margin: 0 auto;
  background-color: #3756CD;
  color: #FFFFFF;
  border-radius: 0 0 10px 10px;
}
.staking .stage-points .stage-points-info .top-stage-points .points-box .points-num,
.staking .stage-points .stage-points-info .bottom-stage-points .points-box .points-num {
  color: #E0ECFF;
  font-size: 24px;
  width: 224px;
  height: 45px;
  white-space: nowrap;
  line-height: 45px;
  margin: 0 auto;
  margin-top: 16px;
}
.staking .stage-points .stage-points-info .top-stage-points .points-box .start-end-stage-time,
.staking .stage-points .stage-points-info .bottom-stage-points .points-box .start-end-stage-time {
  font-size: 12px;
}
@media only screen and (max-width: 759px) {
  .staking .title-timeout {
    padding: 18px;
  }
  .staking .title-timeout .xs-hide {
    display: none;
  }
  .staking .title-timeout .title-time-text .title-text {
    display: block;
  }
  .staking .title-timeout .title-time-text .silder {
    margin-top: 18px;
  }
  .staking .title-timeout .title-time-text .xs-show {
    display: flex;
    justify-content: space-between;
    margin-top: 9px;
    font-size: 14px;
  }
  .staking .title-timeout .title-time-text .xs-show .time {
    color: #FFFFFF;
  }
  .staking .rewards-box {
    padding-left: 18px;
    padding-right: 18px;
    display: block;
  }
  .staking .rewards-box .total-rewards-pool {
    width: 327px;
    height: 193px;
    padding-top: 21px;
    padding-left: 24px;
    background: url("../img/reward-xs.png");
  }
  .staking .rewards-box .total-rewards-pool .total-rewards-num {
    margin-top: 10px;
  }
  .staking .rewards-box .total-rewards-pool .total-rewards-num .toatl-rewards-deri {
    font-size: 42px;
  }
  .staking .rewards-box .open-lock-rewards {
    width: 327px;
    height: 484px;
    display: block;
    margin-top: 8px;
  }
  .staking .rewards-box .open-lock-rewards .top-open-lock-box,
  .staking .rewards-box .open-lock-rewards .bottom-open-lock-box {
    display: block;
    height: 242px;
  }
  .staking .rewards-box .open-lock-rewards .top-open-lock-box .lock-open,
  .staking .rewards-box .open-lock-rewards .bottom-open-lock-box .lock-open {
    width: 327px;
    margin-top: 8px;
  }
  .staking .rewards-box .open-lock-rewards .top-open-lock-box .open,
  .staking .rewards-box .open-lock-rewards .bottom-open-lock-box .open {
    background-image: url("../img/reward-open-xs.png");
  }
  .staking .stage-points {
    margin-top: 39px;
    height: 340px;
  }
  .staking .stage-points .stage-points-info {
    height: 272px;
    border-top: 1px solid #203B60;
  }
  .staking .stage-points .stage-points-info .top-stage-points,
  .staking .stage-points .stage-points-info .bottom-stage-points {
    display: flex;
    border-bottom: 1px solid #203B60;
  }
  .staking .stage-points .stage-points-info .top-stage-points .points-box,
  .staking .stage-points .stage-points-info .bottom-stage-points .points-box {
    width: 187px;
    height: 136px;
    background: rgba(85, 119, 253, 0.1);
    border-right: 1px solid #203B60;
  }
  .staking .stage-points .stage-points-info .top-stage-points .points-box .points-num,
  .staking .stage-points .stage-points-info .bottom-stage-points .points-box .points-num {
    width: 146px;
  }
  .staking .stage-points .stage-points-info .top-stage-points .xs-last,
  .staking .stage-points .stage-points-info .bottom-stage-points .xs-last {
    border: 0;
  }
}
